import React from 'react';
import {Link} from 'react-router-dom';
const ArticalsList =({articals}) => (
    <>
        {articals.map((artical, key) => (
            <Link key={key} to ={`/artical-page/${artical.name}`} >
            <h3>{artical.title}</h3>
            </Link>
        ))}
    </>
);

export default ArticalsList;