import React from 'react';

const UpvoteSection = ({ articalName, upvotes, setArticalInfo }) => {
    const upvoteArtical = async () => {
        const result = await fetch(`/api/articals/${articalName}/upvote`, {
            method: "post",
        });
        const body = await result.json();
        setArticalInfo(body);
    }
    return (
    <div id="upvote-section">
        <button onClick={() => upvoteArtical()}>Add Upvote</button>
        <p>This page has been upvoted {upvotes} times.</p>
    </div>    
    );
}

export default UpvoteSection;