import React, { useState, useEffect} from 'react';
import ArticalsList from '../components/ArticalsList';
import CommentsList from '../components/CommentsList';
import UpvoteSection from '../components/UpvoteSection';
import AddCommentForm from '../components/AddCommentForm';
import articalContent from './artical-content';
import {
    useParams
  } from "react-router-dom";

const ArticalPage = () => {
    let { name } = useParams();
    const artical = articalContent.find(artical => artical.name === name);

    const [articalInfo, setArticalInfo] = useState({ upvotes: 0, comments: [] });

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articals/${name}`);
            const body = await result.json();
            setArticalInfo(body);
        }
        fetchData();
    }, [name]);
    if(!artical) return <h1>Artical Doesn't exist.</h1>

    const otherArticals = articalContent.filter(artical => artical.name !== name);
    return (
        <>
            <h1>{ artical.title } Page Here</h1>
            <UpvoteSection articalName={name} upvotes={articalInfo.upvotes} setArticalInfo={setArticalInfo} />
            <div>
               {artical.content.map((paragraph, key) => (
                   <p key={key} >{paragraph}</p>
               ))}
            </div>
            <CommentsList comments={articalInfo.comments} />
            <AddCommentForm articalName={name} setArticalInfo={setArticalInfo} />
            <h3>Other Articals:</h3>
            <ArticalsList articals={otherArticals} />
        </>
    );
}
export default ArticalPage;